
import api from '../api'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
  name:"register",
  data:function(){
    return {
      register_data:{
        email:"",
        name:'',
        password:'',
        password_confirmation:'',
      },
      phone:'',
      sms_code:''
    }
  },
  components:{
    Group,
    XInput,
    XButton,
    Flexbox,
    FlexboxItem
  },
  created:function(){
   
  },
  methods:{
    submit_register:function(){
      console.log("");
      var self=this;
      var register_data=this.register_data;
      if(!!register_data.email&&!!register_data.name&&!!register_data.password&&!!register_data.password_confirmation&&register_data.password==register_data.password_confirmation){
        api.register_user(register_data).then(res=>{
          console.log(res);
          var res_data=res.data;
          if(res_data.res){
            //
              self.$vux.toast.show({
                text:'<span class="font-normal">'+res_data.req+'</br></span>',
                onHide(){
                  self.$router.push("/login");
                }
              })
          }else{
            var str='';
            for(var key in res_data){
                str+=res_data[key].toString()+'</br>';
            }
              self.$vux.toast.show({
                text:'<span class="font-normal">'+str+'</span>'
              })
          }
        })
      }
    }
  }
}



