
// <keep-alive> </keep-alive>
import * as config from './config/config.js'
import api from './api'
  import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


  export default {
    name: 'app',
    data: function () {
      return {
        popup_login: false,
        page_name: '',
        search_input: '',
        phone: '',
        pwd: '',
        
        intercept_closeable:true,
        // title:'',
        header_conf: {
          title: '',
          is_show: true,
          show_back: false
        },
        navbar: {
          
        }
      }
    },
    computed:{
      
    },
    methods: {
      fetch_data: function () {
        this.popup_login = false;
        this.page_name = this.$route.name;
        this.xheader_handler(this.$route);
        // navbar  active
      },
      init_app:function(){
        var self=this;
        //header
        this.page_name = this.$route.name;
        var header_conf = this.handler_xheader(this.$route.name);
        this.header_conf = header_conf;
        this.navbar=config.app_config.navbar;

         //3s
        setTimeout(function(){
            self.popup_login=self.$store.state.popuplogin.popup_login;
            self.intercept_closeable=config.app_config.intercept_closeable;
            console.log(self.popup_login);
            console.log("");
         },config.app_config.intercept_time)
      },
      popup: function () {
        this.popup_login = !this.popup_login;
      },
      xheader_handler: function (route) {
        var header_conf = this.handler_xheader(route.name);
        this.header_conf = header_conf;
      }
    },
    created: function () {
      this.init_app();
     
    },
    watch: {
      $route: 'fetch_data'
    },
    components: {
      XHeader,
      Tabbar,
      TabbarItem,
      Search,
      Popup,
      Group,
      XInput,
      XButton
    }
  }




