router.afterEach(transition => {
  // NProgress.done();
  util.loading.hide();
  if(ad.is_show){
    setTimeout(function(){
      $(".init-ad").addClass("init-ad-leave");
      setTimeout(function(){
          util.init_ad.hide();
      },750);
    },ad.show_time-750);
  }
});

FastClick.attach(document.body)
