
import XButton from 'vux/src/components/x-button/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import api from '../api'
export default {
    name:'jump',
    data:function(){
        return {
            jump_time:5,
            res:1, //1-- ; 2--- ; 3-- 
            res_msg:',....',
            success_msg:'! !',
            error_msg:'! '
        }
    },
    created:function(){
        this.get_access_token();
    },
    methods:{
        get_access_token:function(){
            var self=this;
            var query=this.$route.query;
            console.log(query);
            alert("1111");
            api.get_token(query).then(res=>{
                console.log(res);
                var res_data=res.data;
                if(res_data.res){
                    self.res_msg=self.success_msg;
                    self.res=2;
                    //self.jump_to_home();
                    window.location.access_token=res_data.data.access_token;
                    window.location.expires_in=res_data.data.expires_in;
                    window.location.refresh_token=res_data.data.refresh_token;
                    window.location.token_type=res_data.data.token_type;
                    window.location.service_time=res_data.service_time;
                }else{
                    self.res_msg=self.error_msg;
                    self.res=3;
                    //self.jump_to_home();
                }
            })
        },
        jump_to_home:function(){
            var self=this;
            self.jump_time=5;
            setInterval(function(){
                self.jump_time--;
                if(self.jump_time<=0){
                    self.$router.push("/");
                }
            },1000)
        }
    },
    components:{
        XButton,
        Icon
    }
}



